<template>
  <div>
    <!-- {{ n }} -->
    <div id="cell" @click="event">
      <template v-if="flag">{{ text }}</template>
      <template v-else></template>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return { flag: false, text: "" };
  },
  props: ["n","isFinish"],
  methods: {
    event() {
      if(this.text!==""){
        return
      }    
      // if(this.result===""){
      //   return
      // }
       if(this.isFinish){
      this.flag = true;
      // 通知父亲app.vue -->
      this.text = this.n % 2 === 0 ? "X" : "O";
      this.$emit("click-event",this.text);
       }    
    }
  }
  // 错误
  // computed: {
  //   text() {
  //     return this.n % 2 === 0 ? "X" : "√"
  //   }
  // }
};
</script>

<style>
#cell {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 100px; */
  font-size: 80px;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
