<template>
  <div class="app">
    <!-- <div>n的值为{{ n }}</div> -->
    <!-- $event 接受子组件$emit事件传过来的值 -->
    <div class="son">
      <div class="row">
        <cell @click-event="event(0,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(1,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(2,$event)" :n="n" :isFinish="isFinish" />
      </div>
      <div class="row">
        <cell @click-event="event(3,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(4,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(5,$event)" :n="n" :isFinish="isFinish" />
      </div>
      <div class="row">
        <cell @click-event="event(6,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(7,$event)" :n="n" :isFinish="isFinish" />
        <cell @click-event="event(8,$event)" :n="n" :isFinish="isFinish" />
      </div>
    </div>

    <!-- <div>{{ map }}</div> -->
    <div @click="c" :result="result" class="control">
      <a href="#">{{ result }}</a>
      </div>
    <!-- <div>{{ isFinish }}</div> -->
  </div>
</template>

<script>
import Cell from "./Cell";
export default {
  components: { Cell },
  data() {
    return {
      n: 0,
      map: [[null, null, null], [null, null, null], [null, null, null]],
      result: "请点击开始游戏",
      isFinish: false
    };
  },
  methods: {
    c() {
      if (this.result.indexOf("结束") !== -1) {
        return;
      }

      this.result = "游戏进行中...";
      this.isFinish = true;
    },
    event(i, text) {
      // i=0 map[0][0]
      // i=1 map[0][1]
      // i=2 map[0][2]
      // i=3 map[1][0]
      // i=4 map[1][1]
      // i=5 map[1][2]
      // i=6 map[2][0]
      // i=7 map[2][1]
      // i=8 map[2][2]
      // if(this.isFinish){
      //   return
      // }
      this.map[Math.floor(i / 3)][i % 3] = text;
      this.n++;
  // console.log(this.map[0].indexOf(null));
  
      this.tell();
      // console.log(`${i}号被点击了，内容是:${text}`);
    },
    tell() {
      const map = this.map;
      for (let i = 0; i < 3; i++) {
        if (
          map[i][0] !== null &&
          map[i][0] === map[i][1] &&
          map[i][1] === map[i][2]
        ) {
          this.result = `游戏结束!!!，${map[i][0]}方获胜,刷新重新开始游戏`;
          // console.log(map[i][0]);

          this.isFinish = false;
        }
      }
      for (let j = 0; j < 3; j++) {
        if (
          map[0][j] !== null &&
          map[0][j] === map[1][j] &&
          map[1][j] === map[2][j]
        ) {
          this.result = `游戏结束!!!，${map[0][j]}方获胜，刷新重新开始游戏`;

          this.isFinish = false;
        }
      }

      if (
        map[0][0] !== null &&
        map[0][0] === map[1][1] &&
        map[1][1] === map[2][2]
      ) {
        this.result = `游戏结束!!!，${map[0][0]}方获胜，刷新重新开始游戏`;
        this.isFinish = false;
      }
      if (
        map[0][2] !== null &&
        map[0][2] === map[1][1] &&
        map[1][1] === map[2][0]
      ) {
        this.result = `游戏结束!!!，${map[0][2]}方获胜，刷新重新开始游戏`;
        this.isFinish = false;
      }
      if(this.map[0].indexOf(null)===-1&&this.map[1].indexOf(null)===-1&this.map[2].indexOf(null)===-1){
        this.result = `游戏结束!!!，你俩平局，刷新重新开始游戏`;
        this.isFinish = false;
      }
      // if(this.result!=="" &&this.result!==null){

      // }
    }
  }
};
</script>

